import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Dashboard from '@/components/Dashboard'

import BookList from '@/components/book/list'
import BookCategoryList from '@/components/bookcategory/list'

import MemberList from '@/components/member/list'

import StudentList from '@/components/student/list'

import UserList from '@/components/user/list'
import UserChangePwd from '@/components/user/changepwd'
import UserProfile from '@/components/user/profile'

// 
const Login = resolve => require(['@/components/Login'], resolve)

Vue.use(Router)

let router = new Router({
// mode: 'history',
  routes: [
    {
      path: '/login',
      name: '',
      component: Login
    },
    {
      path: '/',
      name: 'home',
      component: Home,
      redirect: '/dashboard',
      leaf: true, // 
      menuShow: true,
      iconCls: 'iconfont icon-home', // class
      children: [
        {path: '/dashboard', component: Dashboard, name: '', menuShow: true}
      ]
    },
    {
      path: '/',
      component: Home,
      name: '',
      menuShow: true,
      leaf: true, // 
      iconCls: 'iconfont icon-users', // class
      children: [
        {path: '/user/list', component: UserList, name: '', menuShow: true}
      ]
    },

    {
      path: '/',
      component: Home,
      name: '',
      menuShow: true,
      leaf: true, // 
      iconCls: 'iconfont icon-users', // class
      children: [
        {path: '/member/list', component: MemberList, name: '', menuShow: true}
      ]
    },

    {
      path: '/',
      component: Home,
      name: '',
      menuShow: true,
      leaf: true, // 
      iconCls: 'iconfont icon-users', // class
      children: [
        {path: '/student/list', component: StudentList, name: '', menuShow: true}
      ]
    },

    {
      path: '/',
      component: Home,
      name: '',
      menuShow: true,
      iconCls: 'iconfont icon-books',
      children: [
        {path: '/book/list', component: BookList, name: '', menuShow: true},
        {path: '/book/category', component: BookCategoryList, name: '', menuShow: true}
      ]
    },
    {
      path: '/',
      component: Home,
      name: '',
      menuShow: true,
      iconCls: 'iconfont icon-setting1',
      children: [
        {path: '/user/profile', component: UserProfile, name: '', menuShow: true},
        {path: '/user/changepwd', component: UserChangePwd, name: '', menuShow: true}
      ]
    }
  ]
})

router.beforeEach((to, from, next) => {
  // console.log('to:' + to.path)
  if (to.path.startsWith('/login')) {
    //window.localStorage.removeItem('access-user')
    window.sessionStorage.removeItem('access-user')
    window.sessionStorage.removeItem('token')
    next()
  } else {
    //let user = JSON.parse(window.localStorage.getItem('access-user'))
    let user = JSON.parse(window.sessionStorage.getItem('access-user'))
    if (!user) {
      next({path: '/login'})
    } else {
      next()
    }
  }
})

export default router



