<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="">
      <!---->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true" :model="filters">
          <el-form-item>
            <el-input v-model="filters.name" placeholder="" @keyup.enter.native="handleSearch"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" v-on:click="handleSearch"></el-button>
          </el-form-item>
          <el-form-item>
            <el-input v-model="filters.mobile" placeholder="" @keyup.enter.native="mobileSearch"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" v-on:click="mobileSearch"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="showAddDialog"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->
      <el-table :data="members" highlight-current-row @selection-change="selsChange"
                style="width: 100%;">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column type="index" width="60"></el-table-column>
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="[]">
                <span>{{ props.row.studentInfo }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column prop="nickname" label="" sortable></el-table-column>
        <el-table-column prop="name" label="" sortable></el-table-column>
        <el-table-column prop="sex" label="" sortable></el-table-column>
        <el-table-column prop="address" label="" sortable></el-table-column>
        <el-table-column prop="memtype" label="" sortable></el-table-column>
        <el-table-column prop="mobile" label="" ></el-table-column>
        <el-table-column prop="email" label="" ></el-table-column>
        <el-table-column prop="birthday" label="" ></el-table-column>
        <el-table-column prop="education" label="" ></el-table-column>
        <el-table-column prop="industry" label="" ></el-table-column>
        <el-table-column prop="background" label="" ></el-table-column>
        <el-table-column prop="createtime" label="" width="120" sortable></el-table-column>
        <el-table-column prop="changetime" label="" width="120" sortable></el-table-column>
        <el-table-column prop="balance" label="" ></el-table-column>        
        <el-table-column label="" width="150">
          <template slot-scope="scope">
            <el-button size="small" @click="showEditDialog(scope.$index,scope.row)"></el-button>
            <el-button type="danger" @click="delMember(scope.$index,scope.row)" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <el-col :span="24" class="toolbar">
        <el-button type="danger" @click="batchDeleteMember" :disabled="this.sels.length===0"></el-button>
        <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total"
                       style="float:right;">
        </el-pagination>
      </el-col>

      <el-dialog title="" :visible.sync ="editFormVisible" :close-on-click-modal="false">
        <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
          <el-form-item label="" prop="nickname">
            <el-input v-model="editForm.nickname" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="name" >
            <el-input v-model="editForm.name" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>

          <el-form-item label="" prop="address" auto-complete="off" :disabled="true">
            <el-input v-model="editForm.address" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="memtype" auto-complete="off" :disabled="true">
            <el-input v-model="editForm.memtype" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobile" auto-complete="off" :disabled="true">
            <el-input v-model="editForm.mobile" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email" auto-complete="off" :disabled="true">
            <el-input v-model="editForm.email" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="balance" auto-complete="off" :disabled="true">
            <el-input v-model="editForm.balance" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="editFormVisible = false"></el-button>
          <el-button type="primary" @click.native="editSubmit"></el-button>
        </div>
      </el-dialog>

      <!---->
      <el-dialog title="" :visible.sync ="addFormVisible" :close-on-click-modal="false">
        <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
          <el-form-item label="" prop="nickname">
            <el-input v-model="addForm.nickname" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="name">
            <el-input v-model="addForm.name" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="sex">
            <el-input v-model="addForm.sex" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="address">
            <el-input v-model="addForm.address" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="memtype">
            <el-input v-model="addForm.memtype" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobile">
            <el-input v-model="addForm.mobile" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="addForm.email" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="birthday">
            <el-input v-model="addForm.birthday" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="education">
            <el-input v-model="addForm.education" auto-complete="off"></el-input>
          </el-form-item>          
          <el-form-item label="" prop="industry">
            <el-input v-model="addForm.industry" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="background">
            <el-input v-model="addForm.background" auto-complete="off"></el-input>
          </el-form-item>  
          <el-form-item label="" prop="balance">
            <el-input v-model="addForm.balance" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="school_id">
            <el-input v-model="addForm.school_id" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="inviter_id">
            <el-input v-model="addForm.inviter_id" auto-complete="off"></el-input>
          </el-form-item>          
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="addFormVisible = false"></el-button>
          <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
        </div>
      </el-dialog>

    </el-col>
  </el-row>
</template>
<script>
  import util from '../../common/util'
  import API from '../../api/api_member';

  export default{
    data(){
      return {
        filters: {
          name: '',
          mobile: ''
        },
        members: [],
        total: 0,
        page: 1,
        limit: 10,
        loading: false,
        sels: [], //

        //
        editFormVisible: false,//
        editFormRules: {
          name: [
            {required: true, message: '', trigger: 'blur'}
          ]
        },
        editForm: {
          nickname: '',
          name: '',
          address: '',
          memtype: '',
          mobile: '',
          email: '',
          balance: ''
        },

        //
        addFormVisible: false,//
        addLoading: false,
        addFormRules: {
          name: [
            {required: true, message: '', trigger: 'blur'}
          ],
          nickname: [
            {required: true, message: '', trigger: 'blur'}
          ],
          balance: [
            {required: true, message: '', trigger: 'blur'}
          ],          
          mobile: [
            {required: true, message: '', trigger: 'blur'}
          ]          
        },
        addForm: {
          nickname: '',
          name: '',
          sex: '',
          address: '',
          memtype: '',
          mobile: '',
          email: '',
          birthday: '',
          education: '',
          industry: '',
          background: '',
          balance: '',
          school_id: '',
          inviter_id: ''
        }
      }
    },
    methods: {
      handleCurrentChange(val) {
        this.page = val;
        console.log("val is: ");
        console.log(val)
        this.search();
      },

      mobileSearch(){
        console.log("mobile search");
        if (this.filters.mobile === '') {
          this.total = 0;
          this.page = 1;
          this.search();
        } else {
          this.SearchByMobile();
        }
      },
      SearchByMobile(){
        let that = this;
        that.loading = true;
        that.page = 1;
        that.total = 0;
        let param = that.filters.mobile;
        API.findById(param).then(function (result) {
          that.loading = false;
          if (result && result.members) {
            if (result.members.length > 0) {
               that.members = result.members;
               that.total = result.members.length;
            } else{
               that.$message.error({showClose: true, message: ', ', duration: 5000});
            }
          }
          that.filters.mobile = '';

        }, function (err) {
          that.loading = false;
          that.$message.error({showClose: true, message: err.toString(), duration: 2000});
        }).catch(function (error) {
          that.loading = false;
          console.log(error);
          that.$message.error({showClose: true, message: '', duration: 2000});
        });

      },

      handleSearch(){
        this.total = 0;
        this.page = 1;
        this.search();
      },
      search(){
        let that = this;
        let params = {
          page: that.page,
          limit: 10,
          name: that.filters.name
        };

        that.loading = true;
        API.findList(params).then(function (result) {
          that.loading = false;
          console.log("receive get all members result")
          console.log(result)
          if (result && result.members) {
            if (result.members.length > 0) {
               that.total = result.total;
               that.members = result.members;
            } else{
               that.$message.error({showClose: true, message: '', duration: 5000});
            }
          }

        }, function (err) {
          that.loading = false;
          that.$message.error({showClose: true, message: err.toString(), duration: 2000});
        }).catch(function (error) {
          that.loading = false;
          console.log(error);
          that.$message.error({showClose: true, message: '', duration: 2000});
        });
      },
      selsChange: function (sels) {
        this.sels = sels;
      },
      //
      delMember: function (index, row) {
        let that = this;
        this.$confirm('?', '', {type: 'warning'}).then(() => {
          that.loading = true;
          console.log("row.nickname is");
          console.log(row.nickname);
          API.remove(row.nickname).then(function (result) {
            that.loading = false;
            console.log('result is ');
            console.log(result);
            if (result && parseInt(result.errcode) === 0) {
              that.$message.success({showClose: true, message: '', duration: 2000});
              that.search();
            } else{
              that.$message.success({showClose: true, message: '', duration: 2000});
              that.search();
            }
          }, function (err) {
            that.loading = false;
            that.$message.error({showClose: true, message: err.toString(), duration: 2000});
          }).catch(function (error) {
            that.loading = false;
            console.log(error);
            that.$message.error({showClose: true, message: '', duration: 2000});
          });
        }).catch(() => {
        });
      },
      //
      showEditDialog: function (index, row) {
        this.editFormVisible = true;
        this.editForm = Object.assign({}, row);
      },
      //
      editSubmit: function () {
        let that = this;
        this.$refs.editForm.validate((valid) => {
          if (valid) {
            this.loading = true;
            let para = Object.assign({}, this.editForm);
            console.log("para.nickname is ");
            console.log(para);
            API.update(para.nickname, para).then(function (result) {
              that.loading = false;
              if (result && parseInt(result.errcode) === 0) {
                that.$message.success({showClose: true, message: '', duration: 4000});
                that.$refs['editForm'].resetFields();
                that.editFormVisible = false;
                that.search();
              } else {
                that.$message.error({showClose: true, message: '', duration: 2000});
              }
            }, function (err) {
              that.loading = false;
              that.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }).catch(function (error) {
              that.loading = false;
              console.log(error);
              that.$message.error({showClose: true, message: '', duration: 2000});
            });
          }
        });
      },
      showAddDialog: function () {
        this.addFormVisible = true;
        this.addForm = {
          nickname: '',
          name: '',
          sex: '',
          address: '',
          memtype: '',
          mobile: '',
          email: '',
          birthday: '',
          education: '',
          industry: '',
          background: '',
          balance: '',
          school_id: '',
          inviter_id: ''
        };
      },
      //
      addSubmit: function () {
        let that = this;
        this.$refs.addForm.validate((valid) => {
          if (valid) {
            that.loading = true;
            let para = Object.assign({}, this.addForm);
            para.createtime = new Date().toLocaleDateString();
            para.changetime = new Date().toLocaleDateString();
            console.log("create time is ");
            console.log(para.createTime);
            API.add(para).then(function (result) {
              that.loading = false;
              if (result && parseInt(result.errcode) === 0) {
                that.$message.success({showClose: true, message: '', duration: 2000});
                that.$refs['addForm'].resetFields();
                that.addFormVisible = false;
                that.search();
              } else {
                that.$message.error({showClose: true, message: '', duration: 2000});
              }
            }, function (err) {
              that.loading = false;
              that.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }).catch(function (error) {
              that.loading = false;
              console.log(error);
              that.$message.error({showClose: true, message: '', duration: 2000});
            });

          }
        });
      },
      //
      batchDeleteMember: function () {
        let ids = this.sels.map(item => item.id).toString();
        let that = this;
        this.$confirm('', '', {
          type: 'warning'
        }).then(() => {
          that.loading = true;
          API.removeBatch(ids).then(function (result) {
            that.loading = false;
            if (result && parseInt(result.errcode) === 0) {
              that.$message.success({showClose: true, message: '', duration: 1500});
              that.search();
            }
          }, function (err) {
            that.loading = false;
            that.$message.error({showClose: true, message: err.toString(), duration: 2000});
          }).catch(function (error) {
            that.loading = false;
            console.log(error);
            that.$message.error({showClose: true, message: '', duration: 2000});
          });
        }).catch(() => {

        });
      }
    },
    mounted() {
      this.handleSearch()
    }
  }
</script>

<style>
  .demo-table-expand label {
    font-weight: bold;
  }
</style>



